import React from 'react';
import { isFunction } from 'lodash';
import ControlWrapper from '@devsnc/sn-record-control-wrapper';
import makeClass from 'classnames';
import { isAttrTrue } from '@devsnc/sn-controls-common';

import {
	fitDropdownToViewport,
	fitDropdownToViewportOld
} from '@devsnc/sn-controls-common';

/**
 * More specific version of ControlWrapper that should be used for input-like
 * controls (things with the standard textbox shape + bottom border).
 *
 * Dropdown lives here because the rendering gets messed up when
 * the dropdown is not an immediate sibling to the field.
 */
export default class FieldAndDropdownWrapper extends React.Component {
	dropdownVisible = false;

	/**
	 * Position any newly-created dropdown to fit within the viewport
	 */
	componentDidUpdate(prevProps) {
		if (this.props.ignoreDropdown) return;
		const dropdownWasHidden = !prevProps.dropdown || !prevProps.hasFocus;

		// Only position dropdown when it first appears; not on every update
		if (dropdownWasHidden && this.dropdownElement) {
			let dropdownFitter = this.props.fallbackDropdownBehavior
				? fitDropdownToViewportOld
				: fitDropdownToViewport;

			dropdownFitter(this.dropdownElement.firstChild, this.target).then(() => {
				this.dropdownElement.style.removeProperty('visibility');
				this.dropdownVisible = true;
			});
		} else if (!this.dropdownElement) {
			this.dropdownVisible = false;
		}
	}

	setDropdownRef = node => {
		// Avoid FOUC. Start hidden until we can compute the correct
		// coordinates in componentDidMount
		if (node && !this.dropdownVisible) {
			node.style.setProperty('visibility', 'hidden');
		}

		this.dropdownElement = node;

		if (isFunction(this.props.dropdownRef)) {
			this.props.dropdownRef(node);
		}
	};

	render() {
		let { role, children, dropdown } = this.props;

		return (
			<ControlWrapper {...this.props}>
				<div
					ref={n => (this.target = n)}
					className={makeClass('sn-control-field', {
						'is-hover': this.props.hasHover,
						'read-only': isAttrTrue(this.props.readonly),
						'is-disabled': isAttrTrue(this.props.disabled)
					})}
					role={role}
					aria-expanded={Boolean(dropdown)}
					aria-haspopup="listbox"
				>
					{children}
				</div>
				{dropdown && (
					<div className="dropdown" ref={this.setDropdownRef}>
						{dropdown}
					</div>
				)}
			</ControlWrapper>
		);
	}
}
